.contaioner {
  width: 100%;
  height: 100vh;
  position: relative;
  .blogWrap {
    padding-top:20px;
    p {
      padding-top:10px;
      text-align: center;
      font-size:20px;
      font-weight: bold;
      color:#FFF;
    }
  }
  .blogEdit  {
    width: 90%;
    margin:30px auto;
    text-align: center;
    padding: 20px;
    background-color: #e5d9c7;
    border-radius: 10px;
    textarea {
      width: 100%;
      height:100px;
      padding: 10px;
      resize: none;
      border:none;
      border-radius: 10px;
    }
    .inputWrap {
      display: flex;
      align-content: center;
      justify-content: space-between;
      input {
        padding: 10px;
        border:none;
        background: #FFF;
        border-radius: 10px;
        width: 25%;
      }
      button .icoChk {
        font-size: 24px;
        color:#ffffff;
        &:hover {
          color: #24170B;
        }
      }
    }
  }
}

.blogList {
  width: 90%;
  max-width:1000px;
  margin:auto;
  padding: 20px;
  border-radius: 10px;
  background: #e5d9c7;
  .blogItem {
    background: #FFF;
    padding:10px 20px;
    margin:10px;
    border-radius: 10px;
    p {
      font-size:15px;
    }
    .date {
      display: block;
      color:#AAA;
      padding-bottom:10px;
      font-size: 13px;
    }
    .content {
      font-size: 17px;
    }
    .btnWrap {
      text-align: right;
    }
    button {
      cursor: pointer;
      padding: 2px 5px;
      margin-right:5px;
      font-size: 18px;
      color: #666;
      &:hover {
        color: #0e0c1b;
      }
    }
    textarea {
      width: 100%;
      height: 40px;
      padding:10px;
      resize: none;
      box-sizing: border-box;
    }
  }
}

/* Mobile */
@media only screen and (max-width: 600px) {
  .contaioner {
    .blogWrap {
      padding-top: 2vw;
      p {
        padding-top: 1vw;
        font-size: 3.7vw;
      }
    }
    .blogEdit  {
      margin:4vw auto;
      padding: 3vw;
      border-radius: 2vw;
      textarea {
        height:10vh;
        padding: 3vw;
        border-radius: 2vw;
      }
      .inputWrap {
        input {
          padding: 3vw;
          border-radius: 2vw;
          width: 50%;
        }
        button .icoChk {
          font-size: 7vw;
        }
      }
    }
  }
  .blogList {
    padding: 3vw;
    border-radius: 2vw;
    .blogItem {
      padding:3vw 4vw;
      margin:3vw;
      border-radius: 2vw;
      p {
        font-size:3.5vw;
      }
      .date {
        padding-bottom:2vw;
        font-size: 2.8vw;
      }
      .content {
        font-size:3.8vw;
      }
      button {
        padding: 1vw 2vw;
        margin-right:1vw;
        font-size: 4vw;
      }
      textarea {
        height: 15vw;
        padding:2vw;
        font-size: 3.5vw;
      }
    }
  }
}